*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.content{
    height: 100vh;
}

.register-page{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
}

/* transaction page */
.filters{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    background: #f8f9fa;
    border-radius: 10px;
}

/* Enhanced select dropdowns */
.ant-select {
    width: 150px;
    font-size: 14px;
    border-radius: 8px;
}

.ant-select-selector {
    border-radius: 8px !important;
    border: 1px solid #d9d9d9 !important;
    transition: all 0.3s;
}

.ant-select-selector:hover, .ant-select-selector:focus {
    border-color: #1890ff !important;
    box-shadow: 0 0 5px rgba(24, 144, 255, 0.5);
}

/* Switch icons design */
.switch-icons{
    border: 1px solid #333;
    border-radius: 8px;
    padding: 8px 12px;
    background: #fff;
    transition: all 0.3s;
}

.switch-icons:hover {
    background: #f0f0f0;
    cursor: pointer;
}

.anticon svg {
    font-size: 22px;
    cursor: pointer;
    transition: color 0.3s;
}

.active-icon{
    color: #1890ff;
    font-weight: bold;
}
.inactive-icon{
    color: gray;
} 

/* Enhanced button design */
.btn-primary {
    background: #1890ff;
    border-radius: 8px;
    font-weight: bold;
    padding: 10px 20px;
    font-size: 14px;
    border: none;
    transition: all 0.3s;
}

.btn-primary:hover {
    background: #40a9ff;
    opacity: 0.9;
}

/* Enhanced card design */
.ant-card {
    border-radius: 12px;
    padding: 24px;
    background: #ffffff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Rounded input fields */
.ant-input, .ant-input-password {
    border-radius: 8px;
    border: 1px solid #d9d9d9;
    transition: all 0.3s;
}

.ant-input:focus, .ant-input-password:focus {
    border-color: #1890ff;
    box-shadow: 0 0 5px rgba(24, 144, 255, 0.5);
}

/* Remove red asterisk for required fields */
.ant-form-item-required {
    font-weight: 600;
    color: #333;
}


